.home-icons {
  display: flex;
  justify-content: center;
  gap: 40px;
  padding: 30px 0;
  background: #fff;
}

.icon-card {
  text-align: center;
}

.icon {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: #9e1b32;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.icon img {
  width: 32px;
  height: 32px;
  object-fit: contain;
}

.label {
  margin-top: 8px;
  font-size: 14px;
  color: #333;
}

.icon {
  width: 70px;
  height: 70px;
  background: transparent;   /* âœ… no background */
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}

@media (max-width: 768px) {
  .home-icons {
    flex-wrap: wrap;
    gap: 20px;
    padding: 20px 10px;
  }

  .icon-card {
    width: 80px;
  }

  .label {
    font-size: 13px;
  }
}
